		/* -----------------------------------
			13. Validate Contact Form
		----------------------------------- */

		let validator = function (formId) {

			ContactFormConstraints.setLocale(app_locale);

			let $form = $('#'+formId);
			let formValues = () => validate.collectFormValues($form);
			let validateForm = (fieldsData) => {

				$form.find('.error').removeClass('error');
				$form.find('.error-message').remove();

				let errorMessage = validate(fieldsData, ContactFormConstraints.constraints(), {'fullMessages': false});

				if ('object' === typeof errorMessage) {
					errorMessage = Object.entries(errorMessage);
				}

				if (errorMessage) {
					errorMessage.forEach((messages) => {
						let fieldElement = document.getElementById(formId).elements[messages[0]];
						let $fieldElement = $('#'+fieldElement.id);
						messages[1].forEach(message => {
							$fieldElement.addClass('error');
							$fieldElement.after('<p class="error-message">'+message+'</p>');
						});
					});
				}

				return errorMessage;
			};

			return {
				isValid: () => (!validateForm(formValues())),
				validateForm: () => validateForm(formValues()),
				formValues: () => formValues()
			};
		};

		let successModal = () => {
			$.magnificPopup.open({
				items: {
					src: '#result-success',
					type: 'inline'
				},
				callbacks: {
					close: function () {
						$(".contact-form-area")[0].reset();
					}
				}
			});
		};

		let errorModal = () => {
			$.magnificPopup.open({
				items: {
					src: '#result-error',
					type: 'inline'
				}
			});
		};

		let formValidator = validator(CONTACT_FORM_ID);
		let $form = $('#'+CONTACT_FORM_ID);
		let validateOnKeyPressInit = false;

		$('#contact-submit').on('click', function(e) {
			e.preventDefault();

			if (formValidator.isValid()) {

				$form.block({
					message: ('fr' === app_locale) ? 'Un instant...' : 'Wait...',
					css: BLOCK_UI_CSS
				});

				$.ajax({
					type: 'POST',
					timeout: 5000,
					url: "/contact",
					data: ({...{'action': 'contact'}, ...formValidator.formValues()}),
					success: function(data) {
						if ('ok' === data) {
							successModal();
						} else {
							errorModal();
						}
					},
					error: function () {
						errorModal();
					},
					complete: function () {
						$form.unblock();
					}
				});
			} else {
				// If submit fail: attach "keyup" event listener and validate on every keyup
				if (!validateOnKeyPressInit) {
					validateOnKeyPressInit = true;
					document.getElementById(CONTACT_FORM_ID).addEventListener('keyup', (e) => {
						if ($form.find('input:focus,textarea:focus').length) {
							formValidator.validateForm();
						}
					});
				}
			}
		});

